<template>
  <div id="app">
    <div v-if="isLoaded && !isWinner" id="inner">
      <FortuneWheel
        style="width: 700px"
        :canvas="canvasOptions"
        :prizes="prizes"
        :verify="cavansVerify"
        @rotateStart="onCanvasRotateStart"
        @rotateEnd="onRotateEnd"
        :duration="100"
      >
      </FortuneWheel>
      <audio ref="audio" :src="'src/audio/wheel.mp3'"></audio>
    </div>

    <transition v-if="!isWinner" name="fade">
      <div v-if="isModalOpened" class="modal-container">
        <div class="modal-main">
          <div class="modal-header">
            <div style="position: absolute; margin-top: -20px">
              <Timer
                @onTimesUp="onTimesUp"
                :time="240"
                :isTimerEnabled="isTimerEnabled"
              />
            </div>
            Вопрос №{{ getNumberOfQuestion }}
          </div>
          <div class="modal-body">
            {{ getQuestion }}
          </div>
          <div v-if="!isTimerEnabled" class="submit-button" @click="isTimerEnabled = true" style="background-color: orange">
            Запустить таймер
          </div>
          <transition-group name="fade2" style="display: flex;">
            <div key="3" v-if="isTimerEnabled" class="submit-button" @click="laugh" style="background-color: orange; width: 200px;" >
              Смех
            </div>
            <div key="1" v-if="isTimerEnabled" class="submit-button" @click="submitQuestion">
              Засчитать ответ
            </div>
            <div key="2" v-if="isTimerEnabled" class="submit-button" @click="fail" style="background-color: red; width: 270px;" >
              Неправильный ответ
            </div>
          </transition-group>
          <div class="modal-footer">
            <div class="hint">
              <div style="display: flex; align-items: center; justify-content: space-around; margin-bottom: 15px;">
                <div>Подсказка 1:</div>
                <div v-if="!isFirstHintOpened" class="submit-button-hint" @click="isFirstHintOpened = true">
                  Открыть
                </div>
              </div>
              <div v-if="isFirstHintOpened" class="hint-text">
                <img :src="getHintText(1)" style="max-width: 400px; max-height: 350px;"/>
                <!-- {{ getHintText(1) }} -->
              </div>
              <Timer
                v-else
                @onTimesUp="onTimesUpHint(1)"
                :time="60"
                :isTimerEnabled="isTimerEnabled"
              />
            </div>
            <div class="hint">
              <div style="display: flex; align-items: center; justify-content: space-around; margin-bottom: 15px;">
                <div>Подсказка 2:</div>
                <div v-if="!isSecondHintOpened" class="submit-button-hint" @click="isSecondHintOpened = true">
                  Открыть
                </div>
              </div>
              <div v-if="isSecondHintOpened" class="hint-text">
                <img :src="getHintText(2)" style="max-width: 400px; max-height: 350px;"/>
                <!-- {{ getHintText(2) }} -->
              </div>
              <Timer
                v-else
                @onTimesUp="onTimesUpHint(2)"
                :time="120"
                :isTimerEnabled="isTimerEnabled"
              />
            </div>
            <div class="hint">
              <div style="display: flex; align-items: center; justify-content: space-around; margin-bottom: 15px;">
                <div>Подсказка 3:</div>
                <div v-if="!isThirdHintOpened" class="submit-button-hint" @click="isThirdHintOpened = true">
                  Открыть
                </div>
              </div>
              <div v-if="isThirdHintOpened" class="hint-text">
                <img :src="getHintText(3)" style="max-width: 400px; max-height: 350px;"/>
                <!-- {{ getHintText(3) }} -->
              </div>
              <Timer
                v-else
                @onTimesUp="onTimesUpHint(3)"
                :time="180"
                :isTimerEnabled="isTimerEnabled"
              />
            </div>
          </div>
        </div>
      </div>
    </transition>

    <transition v-if="isWinner" name="fade">
      <div class="winner">
        Поздравляю
      </div>
    </transition>

  </div>
</template>

<script>
import FortuneWheel from "vue-fortune-wheel";
import "vue-fortune-wheel/lib/vue-fortune-wheel.css";
import Timer from './components/Timer'

export default {
  components: {
    FortuneWheel,
    Timer
  },
  data() {
    return {
      cavansVerify: true, // Whether the turntable in canvas mode is enabled for verification
      isWinner: false,
      isTimerEnabled: false,
      timerSound: new Audio(require('/src/audio/timer.mp3')),
      timerTimeout: 0,
      canvasOptions: {
        borderWidth: 6,
        borderColor: "gray",
      },
      isModalOpened: false,
      lastPrize: "Вращай барабан",
      isFirstHintOpened: false,
      isSecondHintOpened: false,
      isThirdHintOpened: false,
      prizes: [
        {
          id: 1,
          name: "#1",
          value: "Елизавета I",
          bgColor: "#304F79",
          color: "#ffffff",
          probability: 15,
          weight: 1,
          hints: [
            {
              id: 1,
              text: 'hint11'
            },
            {
              id: 2,
              text: 'hint12'
            },
            {
              id: 3,
              text: 'hint13'
            },
          ]
        },
        {
          id: 2,
          name: "#2",
          value: "Что удлиняется?",
          bgColor: "#ADFF2F",
          color: "#ffffff",
          probability: 15,
          weight: 1,
          hints: [
            {
              id: 1,
              text: 'hint21'
            },
            {
              id: 2,
              text: 'hint22'
            },
            {
              id: 3,
              text: 'hint23'
            },
          ]
        },
        {
          id: 3,
          name: "#3",
          value: "Женщина, еврей, хоккей и шахматная доска",
          bgColor: "#20B2AA",
          color: "#ffffff",
          probability: 15,
          weight: 1,
          hints: [
            {
              id: 1,
              text: 'hint31'
            },
            {
              id: 2,
              text: 'hint32'
            },
            {
              id: 3,
              text: 'hint33'
            },
          ]
        },
        {
          id: 4,
          name: "#4",
          value: "Цена собственной жизни",
          bgColor: "#584C63",
          color: "#ffffff",
          probability: 15,
          weight: 1,
          hints: [
            {
              id: 1,
              text: 'hint41'
            },
            {
              id: 2,
              text: 'hint42'
            },
            {
              id: 3,
              text: 'hint43'
            },
          ]
        },
        {
          id: 5,
          name: "#5",
          value: "Легенда африканского народа",
          bgColor: "#87CEEB",
          color: "#ffffff",
          probability: 15,
          weight: 1,
          hints: [
            {
              id: 1,
              text: 'hint51'
            },
            {
              id: 2,
              text: 'hint52'
            },
            {
              id: 3,
              text: 'hint53'
            },
          ]
        },
        {
          id: 6,
          name: "#6",
          value: "Анна Каренина",
          bgColor: "#FAEBD7",
          color: "#ffffff",
          probability: 15,
          weight: 1,
          hints: [
            {
              id: 1,
              text: 'hint61'
            },
            {
              id: 2,
              text: 'hint62'
            },
            {
              id: 3,
              text: 'hint63'
            },
          ]
        },
        {
          id: 7,
          name: "#7",
          value: "Музыкальный вопрос",
          bgColor: "#DDA0DD",
          color: "#ffffff",
          probability: 10,
          weight: 1,
          hints: [
            {
              id: 1,
              text: 'hint11'
            },
            {
              id: 2,
              text: 'hint11'
            },
            {
              id: 3,
              text: 'hint11'
            },
          ]
        }
      ],
      probabilityData: {
        6: [20, 20, 15, 15, 15, 15],
        5: [20, 20, 20, 20, 20],
        4: [25, 25, 25, 25],
        3: [30, 30, 40],
        2: [50, 50],
        1: [100]
      },
      isLoaded: true
    };
  },
  computed: {
    getNumberOfQuestion() {
      const arr = this.prizes.find(i => {
        if(i.id === this.lastPrize)
          return true
      })
      if(arr)
        return arr.id
      return null
    },
    getQuestion() {
      const arr = this.prizes.find(i => {
        if(i.id === this.lastPrize)
          return true
      })
      if(arr)
        return arr.value
      return null
    },
  },
  watch: {
    isTimerEnabled() {
      this.timerSound.pause();
      this.timerSound.currentTime = 0;
      clearTimeout(this.timerTimeout)
      if(this.isTimerEnabled){
        this.timerSound.play();
        this.timerTimeout = setTimeout(() => {
          this.timerSound.pause();
          this.timerSound.currentTime = 0;
          this.timerSound.play();
        }, 120000)
      }
    }
  },
  methods: {
    onCanvasRotateStart(rotate) {
      rotate();
      const audio = new Audio(require('/src/audio/wheel.mp3'));
      audio.play();
    },
    onTimesUp() {
      return
    },
    fail() {
      const audio = new Audio(require('/src/audio/fail.mp3'));
      audio.play();
    },
    laugh() {
      const audio = new Audio(require('/src/audio/laugh.mp3'));
      audio.play();
    },
    submitQuestion() {
      if(this.prizes.length === 1){
        const audio = new Audio(require('/src/audio/correct.mp3'));
        audio.play();
        const winner = new Audio(require('/src/audio/winner.mp3'));
        winner.play();
        this.isTimerEnabled = false
        this.isLoaded = true
        this.isModalOpened = false
        this.isWinner = true
        return
      }
      const audio = new Audio(require('/src/audio/correct.mp3'));
      audio.play();
      this.isTimerEnabled = false
      const countOfPrizes = this.prizes.length - 1
      const probData = this.probabilityData[countOfPrizes]
      let j = -1

      this.isFirstHintOpened = false
      this.isSecondHintOpened = false
      this.isThirdHintOpened = false

      this.isLoaded = false

      this.prizes = [...this.prizes].map(i => {
        if(this.lastPrize === i.id)
          return
        else {
          j++
          return {
            ...i,
            probability: probData[j]
          }
        }
      }).filter(i => i !== undefined)


      setTimeout(() =>{
        this.isLoaded = true
        this.isModalOpened = false
      }, 1)
    },
    onTimesUpHint(hintId) {
      if(hintId === 1)
        this.isFirstHintOpened = true
      if(hintId === 2)
        this.isSecondHintOpened = true
      if(hintId === 3)
        this.isThirdHintOpened = true
    },
    getHintText(hintId) {
      const res = this.prizes.find(i => {
        if(i.id === this.lastPrize)
          return true
      }).hints.find(i => {
        if(i.id === hintId)
          return true
      }).text

      return require(`./hints/${res}.jpg`);
    },
    onRotateEnd(prize) {
      this.lastPrize = prize.id;
      setTimeout(() =>{
        this.isModalOpened = true
      }, 300)
      

      // const countOfPrizes = this.prizes.length - 1
      // const probData = this.probabilityData[countOfPrizes]
      // let j = -1

      // this.isLoaded = false
      // this.prizes = [...this.prizes].map(i => {
        
      //   if(prize.id === i.id)
      //     return
      //   else {
      //     j++
      //     return {
      //       ...i,
      //       probability: probData[j]
      //     }
      //   }
      // }).filter(i => i !== undefined)
      // // this.isLoaded = true
      // setTimeout(() =>{
      //   this.isLoaded = true
      // }, 1)

    },
  },
};
</script>

